.CategoryTab{
    border:3px solid #FFF;
    width: 100%;
    margin-bottom: 10px;
    height: 120px;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
}

.CategoryTab:after{
    content: '';
    left: 0;
    bottom: 0;
    position: absolute;
   /* background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 35%);*/
   background-color: rgba(0,0,0,0.1);
    width: 100%;
    height: 100%;
}


.CategoryLinkWrapper{
    width: 100%;
    height: 100%;;
}
.CategoryInnerWrapper{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
}

.TabImg{
    width: 100%;
    height: 100%;;
    position: absolute;
    left:0;
    top: 0;
    
}

.TabImg img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: inherit;
}

.CategoryTab h2{
    position: relative;
    z-index: 2;
    color: #fff;
    padding: 10px;
    font-weight: 400;
    letter-spacing: 0.75px;
    font-size: 18px;
    text-align: left;
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
    text-shadow: 2px 2px 5px #000;
    font-size: 40px;
        letter-spacing: 5px;
        padding: 5px;
    
}

