.ProductListItem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4px 4px;
    flex-wrap: wrap;
    border-left: 5px solid var(--headerColor);
    padding-left: 10px;
    background-color: rgba(0,0,0, 0.2);
    margin-bottom: 6px;
    border-radius: 5px;
    color: #fff;
}

.ProductListItemImage{
    width: calc(100% + 16px);
    height: 250px;
    /*margin-bottom: 1rem;*/
    left: -8px;
    position: relative;
}

.ProductListItemImage img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: 100%;
}

.ProductListItemName{
    max-width: calc(100% - 7ch);
    font-size: 17px;
    margin-top: 8px;
    text-align: left;
    position: relative;
    font-weight: 300;
}

.ProductListItemName img{
    position: absolute;
    width: 60px;
    top: 0;
    left: 95%;
    z-index: 5;
}




