.ProductListItemPrice{
    align-self: flex-start;
    margin-top: 6px;
    background-color: var(--priceColor);
    padding: 8px 5px;
    /*color: #000;*/
    /*border: 1px solid;*/
    position: relative;
    z-index: 20;
    font-size: 19px;
    font-weight: 300;
    border-radius: 5px;
    display: flex;
    align-items: center;
}


.ProductListItemPrice:after{
    content: '';
    width: 0%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: #2B954A;
    position: absolute;
    z-index: -1;
    transition: width 0.5s;
    border-radius: 2px;
}

.ProductListItemPrice.TakeAway:after{
    width: 100%;
}

.ProductListItemBottle{
   /* width: 100%;
    text-align: right;*/
    font-size: 14px;
    font-style: italic;
    position: relative;
    padding-left: 6px;
    font-weight: 300;
    display: flex;
    flex-direction: column;
}
.ProductListItemBottle:before{
    /*
