    */
}

.ProductListItemBottle span{
    font-size: 10px;
}

.ProductListItemDescription{
    width: 100%;
    font-family: sans-serif;
    font-size: 12px;
    padding-left:0px;
    position: relative;
    padding-top: 2px;
    border-top: 1px solid;
}

.ProductListItemDescription:before{
    content: '';
    position: absolute;
    width: 100%;
    border-top: 1px solid var(--priceColor);
    left: 0;
    top: 0;
}

