.FooterWrapper{
    max-width: 450px;
    margin: 0 auto;
}

.Address{
    display: block;
    padding:0 20px;
    text-align: left;
    margin-bottom:10px;
    align-items: center;
    position: relative;
    padding-top: 10px;
}

.Address address{
    font-style: normal;
    font-weight: 100;
    font-size: 16px;
    text-align: center;
}

.Address a{
    display: block;
    font-size: 18px;
    padding: 5px;
    border-radius: 5px;

}


.Footer h3{
    padding-top: 1rem;
    font-weight: 100;
}

.SocialWrap{
    display: flex;
    justify-content: space-around;
}
.Social{
    display: flex;
    justify-content: space-around;
    width: 100px;
    margin: 0 auto;
}

.Social li{
    padding: 0.5rem;
}

.Social li a{
    transform: scale(0.5);
    display: block;
}
.PhonesWrapper{
    padding: 10px 20px;
    background-color: #CBBEB3;
    color: #000;
    width: 90%;
    margin: 0 auto;
    border-radius: 5px;
}

.PhonesWrapper h3{
    padding-top: 0;
    font-size: 20px;
    font-weight: 300;
    color: #104547;
}
.Phones{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 1rem;
}

.Phones > div{
    display: flex;
    flex-direction: column;
    font-size: 10px;
    line-height: 12px;
}
.Phones > div a{
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.65px;
}

.Developer{
    font-size: 12px;
    padding: 0.5rem 0;
    position: relative;
    font-weight: 100;

}

.Developer:before, .Address:before{
    content: "";
    width: 40%;
    height: 1px;
    background-color: #fff;
    left: 30%;
    top:0;
    position: absolute;
}

.Developer h6{
    font-size: 16px;
    font-weight: 100;

}