.intro{
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #931A1F;
  transition: left 0.5s;
}

.intro.hide{
  left: -110vw;
}

.intro-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 5rem 0;
  max-width: 450px;
  margin:0 auto;
}

.intro-image{
   min-width: 300px;
   margin: 0 auto;
}

.intro-image > img{
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

.intro-logo{
  opacity: 0;
  transition: opacity 1s;
  margin-top: auto;
  margin-bottom: 50%;
}

.intro-logo.show{
  opacity: 1;
}

.enter-btn {
  border: 1px solid;
  color: #fff;
  width: 90%;
  margin: 0 auto;
  padding: 17px;
  border-radius: 5px;
  background: #104547;
  font-size: 25px;
  font-weight: 300;
  opacity: 0;
  margin-bottom: 2rem;
  transition: opacity 0.5s;
}
.enter-btn.show{
  opacity: 1;
}

.enter-info{
  color: #fff;
  font-size: 18px;
  font-weight: 300;
  opacity: 0;
  transition: opacity 0.5s;
}

.enter-info.show{
  opacity: 1;
}

.enter-info .phones{
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  align-items: flex-end;
  width: 90%;
  margin: 0 auto;
  margin-top: 8px;
  border-top: 1px solid #fff;
  padding-top: 4px;
}

.enter-info .phones > div{
  display: flex;
  flex-direction: column;
}